*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body{
   
    width: 100%;
    height: 100vh;
  }
  .navbar{
    width: 100%;
    height: 20%;
    background-color: rgb(177, 195, 211);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    box-shadow: 5px 5px 5px 2px rgb(151, 150, 150);
   
  }
  .navbar .heading{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .navbar .heading img{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 0 5px;
  }
  .navbar .heading h1{
    color: rgb(31, 31, 61);
    padding: 0 5px;
  }
  .nav-menu
  {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
  
  }
  .nav-menu a{
    color:  rgb(31, 31, 61);
    text-decoration: none;
    font-size: 20px;
  }
  .nav-menu li{
    list-style: none;
  }
  .nav-link
  {
   transition: 0.7s ease; 
  }
  .nav-link:hover{
    color: dodgerblue;
  }
  section{
    width: 100%;
    height: 85%;
    display: flex;
    align-items: center;
    justify-content: center;
   
    /* background-color: rgb(245, 243, 243); */
    
  }
  .request{
    height: 10%;
    width: 100%;
    text-align: center;
    font-size: 35px;
    color: white;
  }
  section .donate{
   
    height: 500px;
    width: 25%;
    background-color: rgb(248, 244, 244);
    border-radius: 9px;
    box-shadow: 3px 3px 10px 0px rgb(163, 163, 163);
  }
  section .donate h2{
    margin: 3.5px 0;
    margin-left: 20px;
    font-size: 23px;
    color: rgb(31, 31, 61);
  }
  section .donate .type{
    margin-left: 20px;
    height: 7%;
    width: 80%;
    border: 1px solid rgb(166, 164, 164);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    
  }
  section .donate #type{
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 9%;
  }
  section .donate .type input{
    width: 100%;
    font-size: 17px;
    margin-left: 5px;
    border: none;
    outline: none;
    background: none;
    color: rgb(48, 79, 106);
  }
 
  .Button{
    display: flex;
    height: 10%;
    width: 90%;
    margin: 2% 5%;
   
    align-items: center;
    justify-content: flex-end;
  }
  .Button button{
    width: 35%;
    height: 70%;
    background-color: rgb(74, 128, 227);
    border: none;
    outline: none;
    border-radius: 5px;
  }
  .Button button a{
    text-decoration: none;
    color: white;
  }

  section .donate .location{
    margin: 3.5px 0;
    width: 80%;
    font-size: 21px;
    margin-left: 19px;
    border: 1px solid rgb(166, 164, 164);
    outline: none;
    background: none;
    color: rgb(40, 72, 100);
    transition: .3s linear ;
  }
  section .donate .location:hover{
    color: rgb(237, 20, 20);
    font-size: 21px;
  }